.card-gallery {
  padding-top: 72px;
  padding-bottom: 117px;
  @include md {
    padding-bottom: 37px; }

  &__content {}

  &__content-body {} }

.photo-gallery {
  display: flex;
  flex-direction: column;
  padding-bottom: 62px;

  &__title {
    margin-bottom: 72px;
    @include sm {
      margin-bottom: 30px; }
    h4 {
      font-weight: 400;
      font-size: 40px;
      line-height: 130%;
      margin-bottom: 0;
      @include sm {
        font-size: 30px; }
      @include xsm {
        font-size: 20px; } } }

  &__container {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(2, 176px);
    grid-auto-rows: 176px;
    column-gap: 28px;
    row-gap: 113px;
    @include xl {
      grid-template-rows: repeat(3, 132px);
      grid-auto-rows: 132px;
      row-gap: 53px; }
    @include lg {
      grid-template-columns: repeat(4,1fr);
      grid-template-rows: repeat(2, 152px);
      grid-auto-rows: 152px;
      row-gap: 43px; }
    @include md {
      grid-template-rows: repeat(3, 112px);
      grid-auto-rows: 112px; }
    @include sm {
      row-gap: 174px;
      grid-template-columns: repeat(3 , 1fr);
      grid-template-rows: repeat(4, 1fr);
      grid-auto-rows: 1fr;
      row-gap: 24px; } }

  &__item {
    background: #D9D9D9;
    border-radius: 10px;
    position: relative;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 10px; } }

  &__link {
    align-self: flex-end;
    margin-top: 44px; } }

.photo-presentation {
  padding-top: 62px;
  .card-gallery {

    /* .card-gallery__content-body */

    &__content-body {} } }

.photo-presentation {

  &__title {
    margin-bottom: 80px;
    @include sm {
      margin-bottom: 25px; } }

  &__content {} }

.photo-slider {
  display: flex;
  flex-direction: column;
  .circled-btn {
    @include md {
      transform: scale(.8); } }

  &-item {
    display: grid;
    grid-template: 395px 395px / 1fr 390px;
    column-gap: 20px;
    row-gap: 11px;
    @include xxl {
      grid-template-rows: repeat(2, 300px); }
    @include lg {
      grid-template: repeat(2, 240px) / 1fr 335px; }
    @include md {
      grid-template: repeat(2, 200px) / 1fr 245px; }
    @include sm {
      grid-template: repeat(2, 160px) / 1fr 1fr; }
    @include xsm {
      grid-template-rows: repeat(2, 115px); } }
  &-img {
    border-radius: 15px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 15px; } }

  &-img:first-child {
    grid-row-start: 1;
    grid-row-end: 3; }

  &-navigation {
    margin-top: 30px;
    align-self: flex-end;
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 190px;
    @include xl {
      column-gap: 130px; }
    @include md {
      column-gap: 70px; }
    @include sm {
      justify-content: space-between;
      align-self: auto; } } }
