.history {
  padding-bottom: 200px;
  @include xxl {
    padding-bottom: 130px; }
  @include lg {
    padding-bottom: 70px; }
  @include sm {
    padding-bottom: 40px; }

  &__content {
    display: flex;
    justify-content: space-between;
    padding: 39px 125px 12px 45px;
    border-radius: 20px;
    background: $blue-grnt;
    @include lg {
      padding: 39px 35px 12px 45px; }
    @include md {
      padding: 30px 30px; }
    @include sm {
      padding: 15px 15px;
      align-items: center; }
    @include xsm {
      flex-direction: column;
      justify-content: center;
      row-gap: 20px; } }

  &__box {
    @include xsm {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center; } }

  &__title {
    font-weight: 700;
    font-size: 40px;
    line-height: 120%;
    color: $white;
    max-width: 457px;
    margin-bottom: 40px;
    @include lg {
      font-size: 30px; }
    @include md {
      font-size: 20px; }
    @include sm {
      max-width: 220px; }
    @include xsm {
      text-align: center;
      margin-bottom: 20px; } }

  &__link {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background: $white;
    font-weight: 700;
    font-size: 20px;
    color: $black;
    border-radius: 10px;
    padding: 25.5px 26px;
    white-space: nowrap;
    @include lg {
      padding: 20px 20px;
      font-size: 16px; }
    @include md {
      padding: 12px 12px;
      font-size: 12px; }
    @include sm {
      padding: 10px 10px; }
    &:hover {
      color: lighten($black, 10%);
      background: darken($white, 10%); } }

  &__img {
    max-width: 309px;
    @include md {
      max-width: 200px; }
    @include sm {
      max-width: 100px; } } }
