.propose-main {

  &--pt {
    padding-top: 100px; }

  &__content {
    background: $white;
    border-radius: 20px;
    border: 1px solid #8D9DA9;
    box-shadow: 0px 4px 24px 0px rgba(178, 178, 178, 0.25);
    padding: 48px 48px 48px 48px;
    position: relative;
    @include md {
      padding: 25px; } }

  &__content-img {
    position: absolute;
    right: 20px;
    bottom: 0;
    @include xl {
      max-width: 145px; }
    @include lg {
      max-width: 100px; }
    @include sm {
      display: none; } }

  &__footer {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 72px 0 64px;
    column-gap: 41px;
    @include md {
      flex-direction: column;
      justify-content: center;
      row-gap: 30px; } }

  &__footer-wrap {
    display: flex;
    column-gap: 20px;
    @include sm {
      justify-content: center; } } }

.propose-form {

  &__label {
    display: inline-block;
    @include xl {
      display: block; } }

  &>label:first-child {
    margin-bottom: 38px;
    input {
      font-weight: 400;
      font-size: 18px;
      line-height: 130%;
      border: 1px solid $border-cl;
      border-radius: 10px;
      padding: 20px 20px 20px 30px;
      margin-bottom: 10px;
      width: 764px;
      @include xl {
        width: 100%; }
      @include md {
        padding: 15px;
        font-size: 14px; }

      &::placeholder {
        font-weight: 400;
        font-size: 18px;
        line-height: 130%;
        color: $input-cl;
        @include md {
          font-size: 14px; } } } }

  &__wrap {
    border: 1px solid $border-cl;
    border-radius: 10px;
    padding: 22px 20px 15px 33px;
    display: flex;
    flex-direction: column;
    @include md {
      padding: 15px; }

    label:nth-child(1) {
      position: relative;
      margin-bottom: 60px;

      span {
        position: absolute;
        left: 0;
        bottom: -15px;

        font-weight: 400;
        font-size: 18px;
        line-height: 130%;

        color: $input-cl;

        @include md {
          font-size: 14px; } }

      .input-result {
        position: absolute;
        right: 0;
        bottom: 14px; } }

    label:nth-child(2) {
      position: relative;
      input {
        border: none;
        font-weight: 400;
        font-size: 18px;
        line-height: 130%;
        padding: 0;
        padding-right: 175px;
        @include md {
          font-size: 14px;
          padding-right: 150px; }
        @include sm {
          padding-right: 10px; }
        &::placeholder {
          color: $input-cl;
          font-weight: 400;
          font-size: 18px;
          line-height: 130%;
          text-decoration: underline;
          @include md {
            font-size: 14px; } } }

      .input-result {
        position: absolute;
        bottom: 0;
        right: 0; } } }

  &__textarea {
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    border: 1px dotted $input-cl;
    border-width: 0 0 1px 0;
    resize: none;
    padding-right: 175px;
    height: 105px;
    @include md {
      padding-right: 150px;
      font-size: 14px; }
    @include sm {
      padding: 10px; }

    &:placeholder {
      // font-weight: 400
      // font-size: 18px
      // line-height: 130%
      color: $input-cl; } }

  &__checkbox {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    user-select: none;
    font-weight: 400;
    font-size: 22px;
    line-height: 100%;
    margin-top: 10px;
    @include sm {
      font-size: 18px; }

    span {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 14px;
      height: 14px;
      border: 2px solid $black;
      margin-right: 10px;
      border-radius: 3px;
      svg {
        width: 8px;
        height: 8px;
        fill: $black;
        opacity: 0;
        visibility: hidden;
        transition: all 0.3s ease; } }

    input {
      display: none; }

    input:checked + span svg {
      opacity: 1;
      visibility: visible; } }

  &__upload {
    padding-top: 99px; }

  &__upload-box {
    display: flex;
    justify-content: space-between;
    @include md {
      flex-direction: column; } }

  &__upload-title {
    flex: 0 0 498px;
    @include xl {
      flex: 0 0 440px; }
    @include lg {
      flex: 0 0 275px; }
    @include md {
      flex: 0 0 auto;
      margin-bottom: 20px;
      text-align: center; }
    p {
      font-weight: 400;
      font-size: 22px;
      line-height: 120%;
      margin-bottom: 0;
      @include xl {
        font-size: 20px; }
      @include lg {
        font-size: 16px; } } }

  .card-warning {
    flex: 0 0 346px;
    @include lg {
      flex: 0 0 295px; }
    @include md {
      flex: 0 0 auto; }
    @include xsm {
      flex-direction: column;
      align-items: center;
      gap: 10px; } }

  &__footer {
    padding-top: 99px;
    @include md {
      padding-top: 120px; }
    @include sm {
      padding-top: 60px; } }

  &__footer-wrap {
    display: flex;
    column-gap: 46px;
    @include sm {
      justify-content: center; } } }

.input-result {
  font-weight: 400;
  font-size: 18px;
  line-height: 130%;
  text-align: right;
  color: #212121;
  @include lg {
    font-size: 16px; }
  @include sm {
    display: none;
    font-size: 10px; } }

.upload-descr {
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  @include lg {
    font-size: 12px; } }

.photo-upload {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: 176px;
  grid-auto-rows: 176px;
  justify-content: space-between;
  column-gap: 46px;
  row-gap: 40px;
  padding-bottom: 52px;
  @include xxl {
    grid-template-rows: 160px;
    grid-auto-rows: 160px; }
  @include xl {
    grid-template-columns: repeat(4, 1fr); }
  @include lg {
    grid-template-rows: 125px;
    grid-auto-rows: 125px;
    column-gap: 30px; }
  @include md {
    grid-template-columns: repeat(3, 1fr); }
  @include sm {
    grid-template-columns: repeat(2, 1fr); }
  @include xsm {
    grid-template-rows: 106px;
    grid-auto-rows: 106px;
    gap: 15px; }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    border: 3px dashed #D9D9D9;
    overflow: hidden;
    border-radius: 10px;
    row-gap: 20px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 10px; }

    .plus-img {
      width: 31px;
      height: 31px; } }

  &__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    font-size: 0;
    padding: 0; }

  &__preview {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 10px; } } }

.video-upload {
  display: grid;
  grid-template-columns: repeat(2, 410px);
  grid-template-rows: 230px;
  grid-auto-rows: 230px;
  column-gap: 46px;
  row-gap: 40px;
  @include xxl {
    grid-template-columns: repeat(2, 370px); }
  @include xl {
    grid-template: 200px / repeat(2, 340px);
    grid-auto-rows: 200px; }
  @include lg {
    grid-template: 170px / repeat(2, 272px);
    grid-auto-rows: 170px; }
  @include md {
    grid-template: 205px / 1fr;
    grid-auto-rows: 205px; }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    border: 3px dashed #D9D9D9;
    position: relative;
    row-gap: 26px; }

  &__preview {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px; }

  &__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    font-size: 0;
    padding: 0; } }
