.propose-screen {
  padding-top: 65px;
  padding-bottom: 50px;
  @include md {
    padding-top: 40px; }
  @include sm {
    padding-top: 25px;
    padding-bottom: 25px; }

  &__content {}

  &__header {
    display: flex;
    flex-wrap: wrap;
    column-gap: 18px;
    row-gap: 20px; }

  &__title {}

  &__text {
    max-width: 710px;
    p {
      font-weight: 400;
      font-size: 33px;
      line-height: 120%;
      @include xxl {
        font-size: 28px; }
      @include xl {
        font-size: 25px; }
      @include lg {
        font-size: 20px; }
      @include md {
        font-size: 18px; }
      @include sm {
        font-size: 14px; } } }

  &__img {} }
