.breadcrumbs {

  &__list {
    display: flex;
    column-gap: 20px;
    @include sm {
      column-gap: 10px;
      justify-content: space-between; } }

  &__item {}

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    font-size: 20px;
    line-height: 100%;
    color: $black;
    padding: 15px 20px;
    border: 1px solid $black;
    border-radius: 5px;
    position: relative;
    z-index: 1;
    transition: color 0.3s ease;
    @include lg {
      padding: 10px 15px; }
    @include md {
      padding: 8px 13px;
      font-size: 18px; }
    @include sm {
      padding: 6px 10px;
      font-size: 14px; }
    @include xsm {
      padding: 5px 8px;
      font-size: 12px; }
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 100%;
      z-index: -1;
      transition: all 0.3s ease;
      background: $accent-grnt;
      border-radius: 5px;
      opacity: 0;
      visibility: hidden; }
    &:hover {
      color: $white;
      border-color: transparent;
      &::after {
        opacity: 1;
        visibility: visible; } } }

  &__link.active {
    color: $white;
    border-color: transparent;
    &::after {
      opacity: 1;
      visibility: visible; } } }
