.create-post {
  padding-top: 65px;

  &__header {
    display: flex;
    column-gap: 18px;
    flex-wrap: wrap;
    row-gap: 20px; }

  &__title {
    padding-top: 144px;
    @include xxl {
      max-width: 735px; }
    @include xl {
      max-width: 600px; }
    @include lg {
      max-width: 430px;
      padding: 0; }
    @include md {
      text-align: center;
      max-width: 100%;
      margin-bottom: 30px; } }

  &__text-wrap {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    @include sm {
      margin-right: 15px; } }

  &__text {
    span {
      font-size: 33px;
      line-height: 120%;
      @include xl {
        font-size: 24px; }
      @include lg {
        font-size: 20px; }
      @include md {
        font-size: 16px; }
      @include sm {
        font-size: 12px; } }
    span:nth-child(1) {
      font-weight: 400; }
    span:nth-child(2) {
      font-weight: 700; } }

  &__img {
    transform: translate(-40%, -40%);
    @include xxl {
      transform: translate(-26%, -40%); }
    @include lg {
      max-width: 130px;
      transform: translate(-40%, -25%); }
    @include md {
      max-width: 90px; }
    @include sm {
      transform: none; } } }
