.stars-news {
  padding-top: 64px;
  @include sm {
    padding-top: 30px; }

  &__content {}

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between; }

  &__country-filter {
    display: flex;
    align-items: center;
    column-gap: 18px;
    @include md {
      flex-wrap: wrap;
      row-gap: 15px; }
    @include sm {
      row-gap: 15px; } } }

.body-news {
  padding-top: 41px;
  @include xsm {
    padding-top: 25px; }

  &__box {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    @include xl {
 }      // column-gap: 20px

    &-item:nth-child(1) {
      max-width: 626px;
      @include xl {
        max-width: 530px; }
      @include lg {
        max-width: 380px; }
      @include md {
        max-width: 285px; }
      @include sm {
        max-width: 180px; }
      @include xsm {
        max-width: 140px; } } }

  &__title {
    font-weight: 400;
    font-size: 90px;
    line-height: 120%;
    margin-bottom: 40px;
    @include xl {
      font-size: 70px; }
    @include lg {
      font-size: 55px;
      line-height: 110%; }
    @include md {
      font-size: 40px;
      margin-bottom: 30px; }
    @include sm {
      font-size: 25px; }
    @include xsm {
      font-size: 20px; } }

  &__text {
    font-weight: 400;
    font-size: 33px;
    line-height: 120%;
    @include xl {
      font-size: 26px; }
    @include lg {
      font-size: 22px; }
    @include md {
      font-size: 20px; }
    @include sm {
      font-size: 14px; }
    @include xsm {
      font-size: 12px; }
    p {
      margin-bottom: 0; } }

  &__box-item .body-news__img {
    width: 386px;
    height: 384px;
    border-radius: 10px;
    background: rgba(0, 0, 0, 0.1);
    @include xl {
      width: 350px;
      height: 350px; }
    @include lg {
      width: 270px;
      height: 270px; }
    @include md {
      width: 200px;
      height: 200px; }
    @include sm {
      width: 150px;
      height: 150px; }
    @include xsm {
      width: 130px;
      height: 130px; }
    img {
      width: 100%;
      height: 100%;
      border-radius: 10px;
      object-fit: contain; } }

  &__box-images {
    display: grid;
    grid-template-columns: repeat(2, 184px) 385px;
    grid-template-rows: repeat(2, 184px);
    gap: 20px;
    @include xl {
      grid-template: repeat(2, 150px) / repeat(2, 150px) 350px; }
    @include lg {
      grid-template: repeat(2, 110px) / repeat(2, 110px) 215px; }
    @include md {
      grid-template: repeat(2, 100px) / 100px 200px; }
    @include sm {
      grid-template: repeat(2, 75px) / 75px 150px;
      gap: 10px; }
    @include xsm {
      grid-template: repeat(2, 60px) / 60px 120px; } }

  &__img {
    background: rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 10px; } }

  &__box-images .body-news__img:first-child {
    grid-row: 2 / 3;
    grid-column: 1 / 2; }

  &__box-images .body-news__img:nth-child(2) {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
    @include md {
      grid-column: 1 / 2; } }

  &__box-images .body-news__img:nth-child(3) {
    grid-row: 1 / 3;
    grid-column: 3 / 4;
    @include md {
      grid-column: 2 / 3; } }

  &__adv-img {
    align-self: flex-end;
    // max-width: 453px
    // max-height: 408px
    max-width: 330px;
    @include xxl {
      max-width: 270px; }
    @include xl {
      max-width: 200px; }
    @include lg {
      max-width: 180px; }
    @include md {
      max-width: 150px; }
    @include sm {
      max-width: 100px; }
    @include xsm {
      max-width: 85px; }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover; } }

