.header {
   &__menu {
      max-width: 320px;
      margin-left: auto;
      margin-top: 20px;
      &-list {
         height: 0;
         overflow: hidden;
         transition: height 0.6s ease-in-out; }
      &-title {
         cursor: pointer;
         color: #000;
         font-feature-settings: 'clig' off, 'liga' off;
         font-family: Montserrat;
         font-size: 16px;
         font-weight: 700;
         line-height: 120%;
         display: flex;
         justify-content: space-between;
         margin-bottom: 20px;
         img {
            width: 20px;
            height: 20px;
            transform: rotate(90deg); } }
      &-box {
         display: flex;
         justify-content: space-between;
         img {
            width: 20px;
            height: 20px; } }
      &-item {
         &::before {
            content: '';
            width: 100%;
            height: 1px;
            background-color: #000;
            margin: 12px 0;
            display: block; }
         &:first-child {
            &::before {
               display: none; } }
         a {
            color: #212121;
            font-feature-settings: 'clig' off, 'liga' off;
            font-family: Montserrat;
            font-size: 14px;
            font-weight: 700;
            line-height: 120%; } } } }
