.breaking-news {
  background: $accent-grnt;
  padding-top: 51px;
  @include md {
    padding-bottom: 51px; }
  &__content {
    display: flex; }

  &__row {
    .white-btn {
      @include xl {
        padding: 15px 55px; } } }

  &__title {
    h2 {
      font-weight: 400;
      font-size: 90px;
      line-height: 130%;
      color: $white;
      @include xl {
        font-size: 50px; }
      @include lg {
        font-size: 40px; }
      @include md {
        font-size: 30px; } } }

  &__descr {
    max-width: 422px;
    p {
      font-weight: 400;
      font-size: 24px;
      line-height: 120%;
      color: $white;
      margin: 5px 0 120px;
      @include lg {
        font-size: 16px; }
      @include md {
        font-size: 18px;
        margin: 5px 0px 30px; } } }

  &__img {
    max-width: 405px;
    max-height: 461px;

    @include xl {
      margin-left: 30px; }
    @include md {
      display: none; }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover; } } }
