.play {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 162.314px;
  height: 162.314px;
  border: 3px solid $white;
  border-radius: 100%;
  .icon--video-play {
    width: 33px;
    height: 37px;
    fill: $white; } }

.gallery-play {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border: 1px solid $white;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  svg {
    width: 15px;
    height: 20px;
    fill: $white;
    // position: absolute
    // top: 50%
    // left: 50%
 } }    // transform: translate(-50%, -50%)

.card-warning {
  border-radius: 10px;
  background: #FF6060;
  padding: 5px 23px 5px 26px;
  display: flex;
  column-gap: 12px;
  @include xsm {
    padding: 10px; }

  p {
    font-weight: 400;
    font-size: 12px;
    line-height: 120%;
    color: $white;
    margin-bottom: 0;
    @include xsm {
      text-align: center; } }

  svg {
    flex-shrink: 0;
    width: 13.5px;
    height: 13.5px;
    fill: $white; } }

.icon--heart {
  width: 25px;
  height: 25px;
  flex-shrink: 0;
  fill: #eff3f6;
  @include lg {
    width: 20px;
    height: 20px; } }

.icon--heart {
  &.active {
    fill: red; } }

.new {
  display: flex;
  justify-content: center;
  align-items: center;

  font-weight: 700;
  font-size: 18px;
  line-height: 120%;

  width: 94px;
  height: 45px;

  background: $accent-grnt;
  color: $white;

  border-radius: 0 10px;

  @include md {
    width: 75px;
    height: 35px;
    font-size: 16px; } }
