@media (max-width: 1200px) {
   .chat__body {
      display: flex;
      flex-direction: column;
      align-items: center;
      .user-data-top {
         display: grid;
         grid-template-columns: repeat(1, 320px) !important; }
      .profile-nav-left {
         display: flex;
         flex-direction: column;
         align-items: center; } }
   .chat__right {
      position: static; }
   .card {
      &__wrapper {
         display: grid;
         grid-template-columns: repeat(2, 320px);
         justify-content: center;
         gap: 20px; } } }
@media ( max-width: 992px ) {
   .profile__chat {
      &-title {
         font-size: 50px;
         margin-bottom: 60px; } }
   .card {
      &__title {
         font-size: 30px; } } }

@media ( max-width: 767px ) {
   .profile__chat {
      margin-top: 30px;
      &-title {
         font-size: 30px;
         margin-bottom: 30px; } } }
@media (max-width: 700px) {
   .command__wrapper {
      display: flex;
      flex-direction: column;
      align-items: center; }
   .card {
      &__wrapper {
         display: grid;
         grid-template-columns: repeat(1, 280px);
         justify-content: center;
         gap: 20px; } } }
@media ( max-width: 600px ) {
   .card {
      &__title {
         font-size: 20px; } } }
@media ( max-width: 432px ) {
   .profile__chat {
      &-title {
         font-size: 25px; } } }
