.wallet {
   &-spin {
      padding: 100px 0;
      position: relative;
      &__wrapper {
         display: flex;
         justify-content: center; }
      &__modal {
         display: none;
         position: fixed;
         top: 50%;
         left: 50%;
         transform: translateX(-50%);
         z-index: 55; }
      &__priz {
         display: flex;
         align-items: center;
         text-align: center;
         padding: 20px 0;
         flex-direction: column;
         background-color: #fff;
         border-radius: 10px;
         width: 300px;
         margin: 0 auto; } }
   &-hystory {
      &__sort {
         display: flex;
         align-items: center;
         color: #000;
         font-size: 36px;
         font-weight: 400;
         line-height: 120%;
         width: 100%;
         * {
            &:not(:last-child) {
               margin-right: 20px; } } } }
   &-currency {
      padding-bottom: 140px;
      &__list {
         display: grid;
         grid-template-columns: repeat(3, 1fr);
         gap: 28px;
         &-item {
            min-height: 223px;
            border-radius: 10px;
            background: #FFF;
            box-shadow: 0px 4px 14px 0px rgba(0, 0, 0, 0.10);
            padding: 42px 41px 10px 41px;
            display: flex;
            text-align: center;
            flex-direction: column;
            align-items: center;

            color: #000;
            font-size: 18px;
            font-weight: 400;
            line-height: 120%;
            &__pay {
               padding: 18px 80px;
               border-radius: 10px;
               overflow: hidden;
               border: 2px solid;
               border-image: linear-gradient(224deg, #F00 0%, #FFD466 100%);
               border-image-slice: 1;
               margin-bottom: 20px;
               color: #000;
               font-size: 25px;
               font-weight: 700; } } } }
   &-profile {
      padding-top: 100px;
      padding-bottom: 132px;
      &__wrapper {
         display: flex;
         justify-content: space-between; }
      &__box {
         display: flex;
         align-items: center;

         color: #000;
         font-size: 30px;
         font-weight: 700; }
      &__image {
         max-width: 128px;
         border-radius: 10px;
         overflow: hidden;
         margin-right: 28px;
         img {
            width: 100%;
            height: 100%;
            object-fit: cover; } } }
   &__title {
      color: #000;
      font-size: 64px;
      font-weight: 400;
      line-height: 120%; } }

.wallet-spin__container {
   width: 500px;
   height: 500px;
   background-color: #fff;
   border-radius: 50%;
   border: 15px solid #dde;
   position: relative;
   overflow: hidden;
   transition: 5s;

   & div {
      height: 50%;
      width: 200px;
      position: absolute;
      clip-path: polygon(100% 0, 50% 100%, 0 0);
      transform: translateX(-50%);
      transform-origin: bottom;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 20px;
      font-weight: bold;
      font-family: sans-serif;
      color: #fff;
      left: 135px; }

   & .one {
      background-color: red;
      left: 50%; }

   & .two {
      background-color: orange;
      transform: rotate(45deg); }

   & .three {
      background-color: yellow;
      transform: rotate(90deg); }

   & .four {
      background-color: green;
      transform: rotate(135deg); }

   & .five {
      background-color: blue;
      transform: rotate(180deg); }

   & .six {
      background-color: grey;
      transform: rotate(225deg); }

   & .seven {
      background-color: #34495e;
      transform: rotate(270deg); }

   & .eight {
      background-color: #000000;
      transform: rotate(315deg); } }

.arrow {
   position: absolute;
   top: 0;
   left: 50%;
   transform: translateX(-50%);
   color: #fff;

   &::before {
      content: '\1F817';
      font-size: 50px; } }

#spin {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   z-index: 10;
   background-color: #e2e2e2;
   text-transform: uppercase;
   border: 8px solid #fff;
   font-weight: bold;
   font-size: 20px;
   color: #e2e2e2;
   width: 80px;
   border-radius: 50%;
   height: 80px; }
