.style {

  &__title {
    display: inline-block;
    max-width: 529px;
    margin-bottom: 34px;
    @include lg {
      max-width: 350px; }
    @include sm {
      max-width: 220px; } }

  &__content {
    display: grid;
    grid-template-columns: repeat(3, 383px);
    justify-content: space-between;
    @include xxl {
      grid-template-columns: repeat(3, 350px); }
    @include xl {
      grid-template-columns: repeat(3, 290px); }
    @include lg {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      gap: 30px;
      grid-template-columns: none; } }

  &__box {
    @include lg {
      max-width: 330px; }
    @include md {
      max-width: 240px; } }

  &__box:nth-child(2) {
    @include lg {
      order: 1; } }

  &__item {
    position: relative;
    border-radius: 15px;
    height: 527px;
    background: $white;
    padding: 24px 0 0;
    user-select: none;
    @include xl {
      height: 494px; }
    @include md {
      height: 409px; } }

  &-navigation {
    display: flex; }

  &-slider {

    .swiper-slide {
      img {
        width: 100%;
        height: 354px;
        object-fit: contain;
        @include xl {
          height: 330px; }
        @include md {
          height: 270px; } } } }

  &-button-prev {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    position: relative;
    width: 50%;
    height: 75px;
    border-radius: 0 0 0 15px;
    background: $black;
    padding-left: 47px;
    @include md {
      height: 55px;
      padding-left: 30px; }
    svg {
      fill: $white;
      width: 54px;
      height: 11px; } }

  &-button-next {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: relative;
    width: 50%;
    height: 75px;
    border-radius: 0 0 15px 0;
    background: $black;
    padding-right: 47px;
    @include md {
      height: 55px;
      padding-right: 30px; }
    svg {
      width: 53px;
      height: 11px;
      fill: $white; } }

  &__box-descr {
    font-weight: 400;
    font-size: 33px;
    line-height: 130%;
    text-align: center;
    margin-top: 32px;
    @include xl {
      font-size: 25px; }
    @include md {
      font-size: 20px; } }

  &__shop {
    padding: 129px 0 100px; } }
