.header {
  padding: 78px 0 15px;
  @include lg {
    padding-top: 55px; }
  @include sm {
    padding-top: 15px; }

  .logo {
    display: inline-block;
    width: 243px;
    height: 73px;
    @include lg {
      width: 180px;
      height: 50px; }
    @include md {
      width: 130px;
      height: 40px; }
    @include sm {
      width: 100px;
      height: 30px; }
    .icon--logo {
      width: inherit;
      height: inherit;
      fill: $black; } }
  &__box {
    height: 60px;
    padding-right: 10px;
    background-color: #fff;
    border-radius: 15px;
    display: flex;
    align-items: center;
    overflow: hidden;
    &-image {
      margin-right: 10px;
      max-width: 60px;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover; } } }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center; }

  &__actions {
    display: flex;
    align-items: center; }

  &__search {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    margin-right: 14px;
    @include md {
      width: 20px;
      height: 22px; }
    @include sm {
      width: 15px;
      height: 15px;
      margin-right: 10px; }

    .icon--search {
      width: 100%;
      height: 100%; } }

  &__notice {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 22px;
    height: 24px;
    margin-right: 26px;
    position: relative;
    @include md {
      width: 18px;
      height: 22px; }
    @include sm {
      width: 15px;
      height: 15px;
      margin-right: 10px; }

    .icon--notice {
      position: relative;
      z-index: 1;
      width: 100%;
      height: 100%; }

    span {
      position: absolute;
      top: 2px;
      left: 0;
      display: inline-block;
      width: 8px;
      height: 8px;
      background: #FF003D;
      border-radius: 50%;
      opacity: 0;
      visibility: hidden; } }

  &__notice.notice-active {
    span {
      opacity: 1;
      visibility: visible; } }

  &__profile {
    display: flex;
    align-items: center;
    justify-content: center;

    .icon--arrow {
      width: 15px;
      height: 10px;
      @include sm {
        width: 7px;
        height: 7px; } } }

  &__profile-img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 38px;
    height: 38px;
    border-radius: 50%;
    margin-right: 15px;
    background: rgba(0, 0, 0, 0.1);
    @include sm {
      width: 20px;
      height: 20px;
      margin-right: 5px; }
    img {
      width: 100%;
      height: 100%; } }

  &__profile-name {
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: $black;
    margin-right: 15px;
    white-space: nowrap;
    @include md {
      font-size: 18px;
      margin-right: 7px; }
    @include sm {
      font-size: 12px; } } }

.wrapper.black-header {
  .header {
    background: $black;

    .logo {
      .icon--logo {
        fill: $white; } }

    &__search {
      .icon--search {
        fill: $white; } }

    &__notice {
      .icon--notice {
        fill: $white; } }

    &__profile {
      .icon--arrow {
        fill: $white; } }

    &__profile-name {
      color: $white; } } }
