.choose-news {
  padding: 105px 0 126px;
  @include lg {
    padding-bottom: 60px; }
  @include md {
    padding-top: 70px; }
  @include xsm {
    padding-top: 50px; }

  &__content {
    position: relative;

    &-box {
      display: flex;
      column-gap: 35px; } }

  &__title {
    max-width: 1063px;
    margin-bottom: 49px;
    @include xxl {
      max-width: 815px; }
    @include lg {
      max-width: 655px; }
    @include md {
      max-width: 490px; }
    @include sm {
      max-width: 100%;
      text-align: center; }
    h2 {
      margin-bottom: 0; } }

  &__text {
    max-width: 590px;
    margin-bottom: 59px;
    p {
      font-weight: 400;
      font-size: 30px;
      line-height: 120%; } }

  &__unchoosed {
    max-width: 345px;
    span {
      font-weight: 700;
      font-size: 25px;
      line-height: 120%;
      color: #FF0E0E; } }

  &__img {
    position: absolute;
    top: 98px;
    right: -141px;
    max-width: 592px;
    max-height: 380px;
    img {
      width: 100%;
      height: 100%; } } }
