.publication {
  background: $sky-grnt;

  &__content {
    display: flex;
    justify-content: space-between;
    column-gap: 30px; }

  &__body {
    padding-top: 60px;
    @include xl {
      padding-top: 30px; }
    @include lg {
      padding-top: 20px; }
    @include md {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-bottom: 20px; } }

  &__title {
    max-width: 540px;
    margin-bottom: 38px;
    @include md {
      text-align: center; }
    h4 {
      font-weight: 400;
      font-size: 40px;
      line-height: 120%;
      color: $white;
      margin-bottom: 0;
      @include xxl {
        font-size: 34px; }
      @include xl {
        font-size: 28px; }
      @include lg {
        font-size: 23px; }
      @include xsm {
        font-size: 20px; } } }

  &__text-wrap {
    display: inline-flex;
    flex-direction: column;
    row-gap: 20px; }

  &__text {
    display: inline-flex;
    align-items: flex-start;
    column-gap: 11px;
    max-width: 400px;

    p {
      font-weight: 400;
      font-size: 20px;
      line-height: 120%;
      margin-bottom: 0;
      color: $white;
      @include lg {
        font-size: 16px; } }

    .icon--check-white {
      width: 12px;
      height: 10px;
      flex-shrink: 0;
      fill: $white;
      transform: translateY(4px); } }

  &__img {
    display: flex;
    align-items: flex-end;
    @include md {
      display: none; } } }
