// Typography

* {
  margin: 0;
  padding: 0; }

ul {
  margin: 0;
  padding: 0;
  list-style-type: none; }

a {
  transition: all .3s ease;
  &:hover {
    text-decoration: none; }
  &:focus {
    text-decoration: none;
    outline: none; } }

img {
  width: auto;
  max-width: 100%;
  height: auto; }

// Titles
h1, h2, h3, h4, h5, h6 {
	// font-family: inherit
	// line-height: 1.1
	// font-weight: 600
 }	// color: inherit

h1 {
  font-size: 36px; }
h2 {
  font-size: 30px; }
h3 {
  font-size: 24px; }
h4 {
  font-size: 18px; }
h5 {
  font-size: 14px; }
h6 {
  font-size: 12px; }

// Paragraphs
p {
	// margin-bottom: 1em
 }	// color: inherit

a {
  text-decoration: none; }

.title {
  font-weight: 400;
  font-size: 64px;
  line-height: 130%;
  color: $black;
  @include xxl {
    font-size: 50px; }
  @include lg {
    font-size: 40px; }
  @include md {
    font-size: 30px; }
  @include sm {
    font-size: 25px; }
  @include xsm {
    font-size: 20px; }
  h1, h2, h3, h4, h5, h6 {
    font-weight: inherit;
    font-size: inherit;
    line-height: inherit;
    margin-bottom: 0; } }

.more-link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  column-gap: 20px;
  font-weight: 700;
  font-size: 20px;
  color: $c3;
  @include lg {
    font-size: 16px; }
  @include md {
    font-size: 15px;
    column-gap: 10px; }
  @include xsm {
    font-size: 12px; }
  .icon--more-arrow {
    width: 22px;
    height: 11px;
    fill: $c3; }
  &:hover {
    color: lighten($c3, 15%);
    .icon--more-arrow {
      color: lighten($c3, 15%); } } }

.wrapper {
  overflow: hidden;
  max-width: 100%;
  position: relative; }

