
.lich-title {
   margin-bottom: 11px !important; }
.arrow-style-none {
   border-radius: 0 !important;
   border: none !important; }
.chat__slide-active {
   border: 1px solid #FF0000; }
.chat__body {
   position: relative; }
.chat__right {
   position: absolute;
   bottom: -30px;
   right: -77px; }
.chat__username {
   margin-bottom: 40px !important; }
.information-wrapper {
   margin-top: 40px;
   margin-bottom: 50px; }
.chat__block {
   text-decoration: underline;
   text-decoration-color: red;
   display: inline-block; }
.lich__chat {
   margin-top: 15px; }
.lich-chat__slide {
   display: flex;
   flex-direction: column;
   align-items: center; }
.iconDF {
   display: flex; }
.icon-dost {
   margin-top: 20px;
   max-width: 37px;
   img {
      width: 100%;
      height: 100%;
      object-fit: cover; } }

.card {
   margin-top: 48px;
   &__title {
      color: #000;
      font-feature-settings: 'clig' off, 'liga' off;
      font-size: 40px;
      font-weight: 400;
      line-height: 120%;
      margin-bottom: 40px; }
   &__wrapper {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 20px; }
   &__item {
      border-radius: 15px;
      overflow: hidden;
      background: #EFF3F6;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      &:nth-child(2) {
         padding-top: 0; } }
   &__box {
      text-align: center;
      justify-content: flex-end;
      padding: 0 37px;
      padding-top: 20px;
      margin: 0 auto; }
   &__btn {
      margin-top: 20px; } }
@media (max-width: 768px) {
   grid-template-columns: repeat(2, 1fr); }
@media (max-width: 480px) {
   grid-template-columns: 1fr; }

.stick {
   background: linear-gradient(180deg, #11C4FF 0%, #2D4EF5 100%);
   padding: 35px 0 31px 0;
   margin: 100px 0;
   &__wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center; }
   &__title {
      color: #FFF;
      font-feature-settings: 'clig' off, 'liga' off;
      font-size: 30px;
      max-width: 300px;
      margin-bottom: 30px;
      font-weight: 400;
      line-height: 120%; }
   &__btn {
      border-radius: 10px;
      background: #fff;
      border: none;
      width: 200px;
      height: 40px;
      color: #000;
      font-size: 22px;
      font-weight: 700;
      line-height: 109.091%; } }

.command {
   &__wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center; }
   &__box {
      padding: 55px 20px;
      margin-bottom: 76px;
      border-radius: 10px;
      background: #FFF;
      box-shadow: 0px 4px 24px 0px rgba(178, 178, 178, 0.25); }
   &__item {
      display: flex;
      flex-direction: column;
      align-items: center;
      &-image {
         overflow: hidden;
         border-radius: 100%; }
      &-icon {
         img {
            width: 100%;
            height: 100%;
            object-fit: cover; } } }
   &__btn {
      padding: 18px 12px;
      background: transparent;
      border-radius: 10px;
      margin-top: 18px;
      color: #000;
      font-size: 22px;
      font-weight: 700;
      border: 1px solid #000;
      line-height: 109.091%;
      &-active {
         border: none;
         color: #fff;
         background: var(--unnamed, linear-gradient(219deg, #F00 0%, #FFD466 100%)); } }
   &__bottom {
      width: 332px;
      margin: 0 auto;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      &-text {
         color: #000;
         font-feature-settings: 'clig' off, 'liga' off;
         font-size: 25px;
         font-weight: 400;
         line-height: 120%; } } }

.chat {
   margin: 50px 0;
   &__box {
      border-radius: 15px;
      border: 1px solid #8D9DA9;
      background: #EFF3F6;
      box-shadow: 0px 4px 24px 0px rgba(178, 178, 178, 0.25); }
   &__wrapper {
      padding: 20px 40px;
      border-bottom: 1px solid #C8C8C8; }
   &__message {
      padding: 20px 37px;
      border-radius: 12px;
      background: #FFF;
      overflow-wrap: break-word;
      box-shadow: 0px 4px 24px 0px rgba(178, 178, 178, 0.25);
      display: inline-block;
      position: relative;
      max-width: 280px; }
   &__smail {
      position: absolute;
      top: 50%;
      width: 80px;
      height: 80px;
      right: -55px;
      transform: translateY(-50%); }
   &__left {
      margin-bottom: 30px; }
   &__right {
      display: flex;
      justify-content: flex-end;
      margin-bottom: 30px; }
   &__panel {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      width: 100%;
      background-color: #fff;
      padding: 14px 19px;
      border: 1px solid #212121;
      border-radius: 12px;
      filter: drop-shadow(0px 4px 24px rgba(178, 178, 178, 0.25));
      &-buttons {
         margin-top: 11px; } }
   &__text {
      color: #000;
      font-feature-settings: 'clig' off, 'liga' off;
      font-family: Montserrat;
      font-size: 15.283px;
      font-weight: 400;
      line-height: 120%; }
   &__button {
      border-radius: 11.346px;
      border: 0.81px solid #000;
      padding: 6px 13px;
      color: #000;
      font-feature-settings: 'clig' off, 'liga' off;
      background: #fff;
      font-family: Montserrat;
      font-size: 14.588px;
      font-weight: 700;
      line-height: 130%;
      &:hover {
         background: linear-gradient(219deg, #F00 0%, #FFB800 100%);
         color: #fff;
         border: none; } }
   &__form {
      padding: 30px 40px;
      button {
         border: none; }
      &-wrapper {
         display: flex;
         align-items: center; }
      &-input {
         width: 100%;
         position: relative;
         height: 100%;
         margin-right: 19px;
         input {
            padding: 25px 12px;
            border-radius: 14px;
            border: 1px solid #626262;
            background: #FFF; } }
      &-smail {
         position: absolute;
         top: 50%;
         right: 12px;
         width: 41px;
         height: 41px;
         border-radius: 100%;
         transform: translateY(-50%); } } }

.psan {
   position: static; }
.card__mt {
   margin-top: 100px;
   margin-bottom: 100px; }
.chat__message.active {
   border: 1px solid red; }
.profile__chat {
   margin-top: 50px;
   &-title {
      margin-bottom: 100px; } }
.card__message {
   padding: 0; }
